import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';
import * as serviceWorker from './serviceWorker';

import Gallery from './components/gallery/gallery-component';
import VideosPage from './components/pages/videos-page';
import NavbarComponent from './components/navbar/navbar-component';
import DropdownComponent from './components/dropdown/dropdown-component';

if (document.getElementById('navbar-root'))
    ReactDOM.render(<NavbarComponent />, document.getElementById('navbar-root'));

if (document.getElementById('video-page-root'))
    ReactDOM.render(<VideosPage />, document.getElementById('video-page-root'));

if (document.getElementById('gallery-root')) {
    ReactDOM.render(<Gallery />, document.getElementById('gallery-root'))
}

for (let i = 0; i < window.hr__dropdowns.length; i++)
if (document.getElementsByClassName('dropdown')[i]) {
    ReactDOM.render(<DropdownComponent 
                        options = { window.hr__dropdowns[i] }
                    />, document.getElementsByClassName('dropdown')[i]);
}
// If you want your app to work offline and load faster, you can change
// unregister() to register() below. Note this comes with some pitfalls.
// Learn more about service workers: https://bit.ly/CRA-PWA
serviceWorker.unregister();
