import React, { Component } from 'react'

class DropdownComponent extends Component {
    constructor(props) {
        super(props);
        this.state = { 
            selectedOption: props.options.options[0],
            availableOptions: props.options.options,
            displayAvailableOptions: false
        }
    }

    toggleDisplayAvailableOptions = () => {
        this.setState({
            displayAvailableOptions: !this.state.displayAvailableOptions
        });
    }

    setSelectedOption = (option) => {
        this.setState({
            selectedOption: option,
            displayAvailableOptions: false
        });
    }

    renderActionHandler = () => {

        if (this.props.options.actionHandler.type === 'LINK' && this.state.selectedOption.url) {
            return (
                <a href = { this.state.selectedOption.url }>
                    { this.props.options.actionHandler.text }
                </a>
            );
        }

        return (
            <button onClick = { this.props.options.actionHandler.func.bind(this, this.state.selectedOption) }>
                { this.props.options.actionHandler.text }
            </button>
        )

    }

    render() { 

        let renderAvailableOptions = this.state.availableOptions.filter( (o, i) => {
            return o.id !== this.state.selectedOption.id;
        }).map( (o, i) => {
            return (
                <button key = { o.id } onClick = { this.setSelectedOption.bind(this, o) }>{ o.text }</button>
            )
        } );

        return ( 
            <div className = "dropdown-wrapper">
                <button className = "selected_item" onClick = { this.toggleDisplayAvailableOptions }>{ this.state.selectedOption.text }</button>
                
                { this.renderActionHandler() }
                
                { (this.state.displayAvailableOptions) && (
                    <div className = "options-list">
                        { renderAvailableOptions }
                    </div>
                )}
            </div>

         );
    }
}
 
export default DropdownComponent;
