import React, { Component } from 'react';
import ReactDOM from 'react-dom';

import VideoModal from './video-modal';

class VideoComponent extends Component {
    constructor(props) {
        super(props);
        this.state = {  }
    }

    displayVideoModal = () => {
        if ( document.getElementById('video') )
            document.body.removeChild(document.getElementById('video_root'));
        
        let d = document.createElement('div');
        d.id = 'video_root';

        document.body.appendChild(d);

        ReactDOM.render(<VideoModal 
            video = { this.props.video }
        />, document.getElementById('video_root'));
    }

    render() { 
        return ( 
            <div className = "video-thumbnail" onClick = { this.displayVideoModal }>
                <img 
                    alt = "video thumbnail" 
                    src = { "http://img.youtube.com/vi/" + this.props.video.src + "/0.jpg" }
                />

                <div className = "play-btn" onClick = { this.displayVideoModal }></div>
            </div>
         );
    }
}
 
export default VideoComponent;