import React, { Component } from 'react';
import VideoComponent from './../video-modal/video-component';

class VideosPage extends Component {
    constructor(props) {
        super(props);
        this.state = { 
            filterMode: 'NONE',
            viewBy: 'GAME',
            loading: true
         }
    }

    getAllVids = () => {
        
        let t = [];

        for (let games of window.hr__vids.games) {

            t = t.concat(games.videos);

        }

        return t;
    }

    filterVids = () => {

        let vids = this.getAllVids();

        switch (this.state.filterMode) {
            case 'TRAILER':
                return vids.filter((v, i) => {
                    return v.CATEGORY === 2
                });
            case 'INTRO':
                return vids.filter((v, i) => {
                    return v.CATEGORY === 3
                });
            case 'ECOLOGY':
                return vids.filter((v, i) => {
                    return v.CATEGORY === 4
                });
            default:
                return vids;
        }   
    }

    setFilter = (filter_mode) => {
        if (this.state.filterMode === filter_mode)
            return;
        
        if (this.state.viewBy === 'GAME')
            this.setState({
                filterMode: filter_mode,
                viewBy: 'CATEGORY'
            });
        else if (filter_mode === 'NONE' 
                    && this.state.viewBy === 'CATEGORY')
            this.setState({
                filterMode: filter_mode,
                viewBy: 'GAME'
            });
        else
            this.setState({
                filterMode: filter_mode
            });
    }

    renderByCategory = () => { 

        let v = this.filterVids();
        let renderVids;

        if (v.length < 1)
            renderVids = ( 
                <div className = "error empty">
                    No Videos Available
                </div> 
            );
        else
            renderVids = this.filterVids().map( (v, i) => {
                    return ( 
                        <VideoComponent key = { i } video = { v } /> 
                    );
                });

        return ( 
            <div className = "videos-wrapper">
                <h3>
                    { this.state.filterMode }
                </h3>
                <div className = "videos-inner">
                    { renderVids }
                </div>
            </div>
        );
    }

    renderButtons = () => {

        return window.hr__vids.categories.map( (c, i) => {
            return ( 
                <button key = { i } onClick = { this.setFilter.bind(this, c.CATEGORY) }>
                    { c.CATEGORY }
                </button>
            )
        });
    }

    renderByGame = () => {
        let renderCont = window.hr__vids.games.map((g, i) => {

            let v = g.videos.map( (v, j) => {

                return ( <VideoComponent key = { j } video = { v } /> );

            } );

            return (
                <div key = { g.id } className = "videos-wrapper">
                    <h3>{ g.name }</h3>
                    <div className = "videos-inner">
                        { v }
                    </div>
                </div>
            );

        });

        return (
            <React.Fragment>
                { renderCont }
            </React.Fragment>
        );
    }

    componentDidMount () {
        if (window.hr__vids) {
            this.setState({
                loading: false
            });
        }
    }

    render() { 

        if (this.state.loading) {
            return (
                <div className = "loading">Now Loading...</div>
            )
        }

        return (
            <div className = "videos-list">
                <div className = "controls-wrapper">
                   <span>Sort By</span> 
                   { this.renderButtons() }
                </div>
                <div className = "all-videos-wrapper">
                    { (this.state.viewBy === 'CATEGORY') ? this.renderByCategory() : this.renderByGame() }
                </div>
            </div>
        )
    }
}
 
export default VideosPage;