import React, { Component } from 'react'

class NavbarComponent extends Component {
    constructor(props) {
        super(props);
        this.state = { 
            doDisplayMenu: false,
            currentWindowDimensions: {
                x: Math.max(document.documentElement.clientWidth, window.innerWidth || 0),
                y: Math.max(document.documentElement.clientHeight, window.innerHeight || 0)
            },
            MOBILE_BREAKPOINT: 1050
         }
    }

    componentDidMount () {
        window.addEventListener('resize', () => {
            let curr_window_dimensions = {
                x: Math.max(document.documentElement.clientWidth, window.innerWidth || 0),
                y: Math.max(document.documentElement.clientHeight, window.innerHeight || 0)
            }

            if (curr_window_dimensions.x > this.state.MOBILE_BREAKPOINT
                && !this.state.doDisplayMenu)
                this.setState({
                    currentWindowDimensions: curr_window_dimensions,
                    doDisplayMenu: true
                });
            else {
                this.setState({
                    currentWindowDimensions: curr_window_dimensions
                });
            }
        });
    }

    toggleMenu = () => {
        this.setState({
            doDisplayMenu: !this.state.doDisplayMenu
        });
    }

    render() { 
        return ( 
            <header id = "nav-top">
                <a href="/" id = "logo-top">
                    Hunting REDUX
                </a>

                { (this.state.currentWindowDimensions.x > this.state.MOBILE_BREAKPOINT 
                    || this.state.doDisplayMenu) && (
                <div className = "nav__link_wrapper">

                    <a href="/releases">Releases</a>

                    <a href="/mhp3rd">MHP3rd Redux</a>

                    <a href = "/videos">Videos</a>

                    <a href="/about">About</a>

                    <a href="https://ppsspp.org" className="ppsspp_logo" >Get PPSSPP</a>

                </div>
                    )}

                <div className = "dl_links">
                    <a href="/" className = "dl-btn">Download Texture Pack</a>
                    <button onClick = { this.toggleMenu } className = "nav__hamburger_menu">
                        Menu
                    </button>
                </div>
            </header>
         );
    }
}
 
export default NavbarComponent;